import React, { Component } from 'react'
import integralStyle from "../../scss/index.module.css"
import {DatePicker,Table } from "antd"
const {  RangePicker } = DatePicker;
//

const columns = [
    {
      title: '',
      dataIndex: 'name',
      width: 150,
    },
    {
      title: '',
      dataIndex: 'age',
      width: 150,
    },
    {
      title: '',
      dataIndex: 'address',
      width: 150,
    },
    {
      title: '',
      dataIndex: 'classify',
      width: 150,
    },
    {
      title: '',
      dataIndex: 'filter',
      width: 150,
    },
    {
      title: '',
      dataIndex: 'shenhe',
    },
  ];
  
  const data = [];
  for (let i = 0; i < 40; i++) {
    data.push({
      key: i,
      name: ` ${i}`,
      age: i+1,
      address: `. ${i}`,
      classify:``,
      filter:``,
      shenhe:``,
    });
  }
export default class TabsAudit extends Component {
    state={      
        tab:[
                {
                    title:""
                },
                {
                    title:""
                },
                {
                    title:""
                },
                {
                    title:""
                },
                {
                    title:""
                },
            ]
    }
    render() {
        let {tab} = this.state;
        return (
            <div className={integralStyle['integral-wrapper']}>
                <div className={integralStyle['integral-tab']}>
                    {
                        tab.map((item,index)=><p key={index}>{item.title}</p>)
                    }
                </div>
                <div className={integralStyle['integral-filter']}>
                    <button></button>
                    <p onClick={()=>this.waitCheck()}></p>
                    <p onClick={()=>this.finishCheck()}></p>
                    <RangePicker
                        className={integralStyle['integral-date']}
                        showTime
                        format="YYYY/MM/DD"
                        onChange={(e)=>this.dateChange(e)}
                    />
                    <button className={integralStyle['integral-btn']}></button>
                </div>
                <div className={integralStyle['integral-data']}>
                    <div>
                        <Table 
                            className={integralStyle['integral-table']}
                            columns={columns} 
                            dataSource={data} 
                            pagination={{ pageSize: 10 }} 
                            scroll={{ y:350 }} />
                    </div>
                </div>
            </div>
        )
    }
    waitCheck(){    
        console.log("")
    }
    finishCheck(){
        console.log("")
    }
    dateChange(e){
        console.log(e)
        
    }
}

